.App {
  text-align: center;
  margin-top: 50px;
}

.table-container {
  display: flex;
  justify-content: center;
}

.table-box {
  border: 1px solid #ccc;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

table {
  width: 100%;
}

th, td {
  padding: 8px;
}

th {
  background-color: #f2f2f2;
  color: red; /* Set heading text color to orange */
}

tbody tr:nth-child(even) {
  background-color: #f2f2f2;
}


